.list-item-delete {
  right: 24px;
  bottom: 21px;
  font-size: 20px;
  width: 68px;
  height: 60px;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  padding-right: 24px;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  /*transform: translateY(-3px);*/
}

.list-item-delete-icon {
  height: 20px;
  width: auto;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}

.list-item-delete:hover .list-item-delete-icon {
  -webkit-transform: scale(1.15);
      -ms-transform: scale(1.15);
          transform: scale(1.15);
}



